:root{
    --PrimaryColor: hsl(199,100%,33%);
    --SecondaryColor: hsl(187,85%,43%);
    --gradientColor: linear-gradient(to right,hsl(187,85%,43%),hsl(199,100%,33%));
    --whiteColor: hsl(0,0%,100%);
    --blackColor: hsl(201,33%,16%);
    --textColor: hsl(240,4%,36%);
    --whiteColorDeam:hsl(0,0%,93%);
    --greyText:rgb(190,190,190);
    --inputColor: rgb(239,239,239);
    --bodyColor:rgb(240,240,246);
    --cardBG:rgb(225,225,235);
}

.home{
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 3rem;
    .overlay{
        position: absolute;
        width: 100%;
        height: 100%;
        color: var(--textColor);
        top: 0;
        bottom: 0;
        z-index: 1;
    }
    .video{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        bottom: 0;
        object-fit: cover;
    }
   

    .homeContent{
        width: 100%;
        height: max-content;
        padding: 8rem 1.5rem 3rem;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-self: center;
        margin: auto;
        color: var(--whiteColor);
        z-index: 100;
        .textDiv{
            padding: 2rem 0;
            .smallText{
                font-size: 14px;
                text-transform: uppercase;
            }
            .homeTittle{
                font-size: 1.8rem;
                position: relative;
                
            }
        }

        .cardDiv{
            background: var(--whiteColor);
            padding: 2rem 1rem;
            border-radius: 10px;
            gap:  1rem;
            position: relative;
            margin-bottom: 3rem;
            label{
                display: block;
                color: var(--textColor);
                padding-bottom: 10px;
                font-size: 13px;
                font-weight: 500;
            }
            .label_total{
                justify-content: center;
                align-items: center;

            }
            .total{
                font-size: 18px;
                color: var(--textColor);
                padding-bottom: 10px;
            }
            .input{
                height: 40px;
                padding: 0 1rem;
                background: var(--inputColor);
                border-radius: 3rem;

                input{
                    border: none;
                    outline: none;
                    background: none;
                    width: 100%;
                    font-size: 13px;
                    font-weight: 500;
                    &::placeholder{
                        font-size: 13px;
                    }
                }
                .icon{
                    font-size: 18px;
                    font-weight: 500;
                    color: var(--PrimaryColor);
                }
                .input[type=range]{
                    color: var(--PrimaryColor);
                }
            }
            
            .searchOptions{
                position: absolute;
                background: var(--gradientColor);
                width: max-content;
                bottom: -0.7rem;
                column-gap: 0.8rem ;
                padding: 5px 1rem;
                border-radius: 3rem;
                left: 50%;
                transform: translate(-50%);
                justify-content: center;
                display: inline-flex;
                cursor: pointer;
                
                &:hover{
                    background: var(--PrimaryColor);
                }
                .icon{
                    font-size: 18px;
                }
                span{
                    font-size: 16px;
                }
            }

        }

        .homeFooterIcons{
            width: 100%;
            justify-content: space-between;
            
            .icon{
                margin: 0 5px;
                font-size: 20px;
                color: var(--whiteColor);
                font-weight: 500;

                &:hover{
                    color: var(--PrimaryColor);
                }
            }
        }
    }
    

}

// media queries ========================================>

@media screen and (min-width:500px) {
    .home{

        .homeContent{
            
            .smallText{
                font-size: 16px;
            }
            .homeTittle{
                position: relative;
                font-size: 2.3rem;
                width: max-content;

                &::after{
                    position: absolute;
                    content: '';
                    background: var(--PrimaryColor);
                    border-radius: 5px;
                    height: 5px;
                    width: 105px;
                    right: 0px;
                    bottom: -2px;
                    z-index: -1;
                }
            }
            .cardDiv{
                grid-template-columns: repeat(2,1fr);
            }
        }

    }
}

@media screen and (min-width:660px) {
    .home{
        .homeContent{

            .cardDiv{
                padding: 2rem 1rem;
                grid-template-columns: repeat(3,1fr);

            }
        }
    }
}

@media screen and (min-width:940px) {
    .home{
        height: 70vh;
        
    }
}
@media screen and (min-width:1240px) {
    .home{
        height: 95vh;
        .homeContent{
            padding-left: 0;
            padding-right: 0;
            .cardDiv{
                padding: 2rem 1rem;
                grid-template-columns: repeat(3,1fr);

                .input{
                    margin-top: 1rem;
                }

                .searchOptions{
                    bottom: -1rem;
                    padding: 8px 1rem;

                    span{
                        font-size: 17px;
                    }
                }
            }
            
        }
    }
}